import {NgModule, ModuleWithProviders} from '@angular/core';
import {CommonModule} from '@angular/common';
import {Routes, RouterModule} from '@angular/router';
import {FormsModule} from "@angular/forms";
import {CheckoutComponent} from "./checkout.component";
import {ShippingCostModalComponent} from "../../components/shipping-cost-modal/shipping-cost-modal.component";

//Định nghĩa router riêng cho module này
const routing: Routes = [
  {
    path: '',
    component: CheckoutComponent
  }
];

//forChild -> Vì router này sẽ được load như một router con
//cho nên chúng ta định nghĩa forChild cho router này
const Routing: ModuleWithProviders = RouterModule.forChild(routing);


@NgModule({
  declarations: [CheckoutComponent, ShippingCostModalComponent],
  imports: [CommonModule, FormsModule, Routing],
  providers: [],
})
export class CheckoutModule {
}



