import {Component} from '@angular/core';
import {ShippingService} from "../../services/shipping.service";

declare var $: any;
@Component({
	moduleId: module.id,
	selector: 'shipping-cost-modal',
	templateUrl: 'shipping-cost-modal.component.html',
	providers: [ShippingService],
})

export class ShippingCostModalComponent {
	private shippings: any;
	$element;

	constructor(private shippingService: ShippingService) {
		this.shippingService.getAll().subscribe(shippings => {
			this.shippings = shippings;
		})
	}

	public showModal() {
		this.$element.modal('show');
	}

	ngOnInit() {
		this.$element = $('#shipping-cost-modal');
	}


}



