import {Component, ViewChild} from '@angular/core';
import {CartService} from "../../services/cart.service";
import {Product} from "../../models/Product";
import {Info} from "../../models/Info";
import {BookingService} from "../../services/booking.service";
import {ShippingCostModalComponent} from "../../components/shipping-cost-modal/shipping-cost-modal.component";
@Component({
	moduleId: module.id,
	selector: 'my-checkout',
	templateUrl: 'checkout.component.html',
	providers: [CartService, BookingService]

})

export class CheckoutComponent {
	cart: Product[];
	step = 1;
	totalPriceCart: number;
	totalPriceCartComplite: number;
	info = new Info();
	@ViewChild(ShippingCostModalComponent) shippingModal: ShippingCostModalComponent;

	constructor(private cartService: CartService, private bookingService: BookingService) {
		this.cart = this.cartService.getAll();
		this.totalPriceCart = this.cartService.getTotalPriceCart();
		console.log('++++++++', this.totalPriceCart);
		this.cartService.getSubject().subscribe(message => {
			this.cart = this.cartService.getAll();
			this.totalPriceCart = this.cartService.getTotalPriceCart();
			console.log('++++++++', this.cart);
			console.log('message ', message);
		});
	}

	showShippingCost() {
		this.shippingModal.showModal();
	}

	add = (slug) => {
		this.cartService.addQuanlityCart(slug, 1);
	};
	subtract = (slug) => {
		this.cartService.subtractQuanlityCart(slug, 1);
	};

	removeCart = (slug) => {
		this.cartService.removeCart(slug);
	};

	nextStep = () => {
		if (this.step == 2) {
			this.info['name'] = this.info['firstName'] + this.info['lastName'];
			this.info['cart'] = this.cart;
			this.bookingService.save(this.info).subscribe(response=> {
				console.log('res', response);
				this.step += 1;
				this.totalPriceCartComplite = this.totalPriceCart;
				this.cartService.removeAll();
			}, function (err) {
				window.alert('Có lỗi xảy ra');
				console.log(err);
			})
		}else{
			this.step += 1;

		}

	};
	goStep = (step) => {
		if (this.step > step) {
			this.step = step;
		}

	};

}



