import {Injectable} from '@angular/core'
import {HttpClient} from "./http-client";

@Injectable()
export class ShippingService {
	constructor(private httpClient: HttpClient) {

	}
	getAll() {
		return this.httpClient.get('/api/shippings');
	}
}



