import {Injectable} from '@angular/core';
import {HttpClient} from "./http-client";

@Injectable()
export class BookingService {
	constructor(private httpClient: HttpClient) {
	}

	save(info) {
		return this.httpClient.post('api/bookings', info);
	}

}



